.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

@media (min-width: 1200px) {
  .container-xl,
.container-lg,
.container-md,
.container-sm,
.container {
    max-width: 1170px;
  }
}
@media (max-width: 991px) {
  .header_full {
    display: none;
  }
}

.header__menu {
  background-color: #fafafa;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  padding-top: 12px;
  padding-bottom: 9px;
  border-bottom: 2px solid #e6e6e6;
  box-shadow: 0px 5px 16px 0px #e6e6e6;
}
.header__menu_one {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu_one a {
  font-family: "Roboto", sans-serif;
  color: #777777;
}
.header__menu_one a:hover {
